.pz-question-block {
  padding: 20px;
  border-radius: 2px;
  border: 1px solid #d6d6d6;
  background-color: #f1f1f1;
  width: 500px;

  .pz-question-text {
    font-size: 18px;
    font-weight: 700;
  }

  .pz-star-empty {
    color: #ffd700;
    position: relative;
    top: -3px;
    transition: transform 0.3s ease-out;

    &:hover {
      transform: scale(1.25) rotate(12deg);
    }
  }

  .pz-delete {
    color: #ff0000;
    position: relative;
    top: -5px;

    &:hover {
      color: #c20000;
    }
  }
}
